<route>
{
    meta: {
        layout: "main",
        title: "Arco",
        parentTitle: "MeltUI",
        icon: "config",
        parentIcon: "eye",
    }
}
</route>

<template>
    <ArcoCrudTable
        class="mt-2"
        :options="options"
        :schema="schema"
        @showItem="showItem"
    />
</template>

<script setup>
import {
    ArcoCrudTable,
    FormSchema,
    CrudOptions
} from 'meltui'

import { Modal } from '@arco-design/web-vue'

const searchable = ref(true)
const editable = ref(false)
let options = ref(new CrudOptions()
    .edit(editable.value) // 
    // .header(false).visible(false) // 
    .row().hover().border().stripe()
    .size().small()
    .layout()
    .search(searchable.value).inline().cols().sm(1).md(2).xl(2)
    // .row().selection().radioType()
    .row().selection().checkboxType().checkAll() // .currentOnly(false)
    // .row().expand().width(50).title('').render("{{ record.key % 2 === 1 ? ' is' + record.name + ',  ' + record.address : JSON.stringify(record, null, 2)  }}")
    // @fix   --> v-model:selected-keys
    // .body().virtualList().height(400)
    // .body().scroll().y(400)
    .column().resizable()   // .body().scroll().x(1000)

    .baseUrl('https://mock.apifox.cn/m1/1087009-0-default/api')
    .fetchList().get('/v1/fetchList') // 
    .viewOperation().fetch().get('/v1/fetch') // 1
    .editOperation().update().post('/v1/update') // 1
    .editBatchOperation().updateBatch().post('/v1/updateBatch') // 
    .addOperation().save().post('/v1/save') // 1
    .removeOperation().delete().post('/v1/delete') // 1
    .needConfirm().confirmText('?') // 
    .removeBatchOperation().deleteBatch().post('/v1/deleteBatch') // 
    .customOperation('').clickEmit('showItem') // 

    // .customOperation("2").clickEmit("showItem")
    .parse())

console.log(options.value)

const schema = ref({
    name: new FormSchema()
        .title().upperFirst()
        .width(200)
        .left().format('{{ \'[No.\' + (rowIndex+1)  + \']\' + record.name }}')
        .readonly()
        .column().fixed().left()
        .input().placeholder('').clearable()
        .searchable() // .placeholder("{{ '' + column.title }}")
        .parse(),
    salary: new FormSchema()
        .title('')
        .width(150).center()
        .inputNumber().placeholder('').clearable(false)
        // .props({
        //     allowClear: true
        // })
        .sortable().asc().desc()
        .filterable()
        .gt(20000)
        .gt([25000, 1000000])
        // .filter("{{ record.salary > value[0] }}")
        .searchable().advancedOnly() // .placeholder("{{ '' + column.title }}")
        .parse(),

    address: new FormSchema()
        .title('')
        .width(150).center()
        .filterable()
        // .startsWith("").startsWith("35 Park Road")
        // .startsWith(["", "35 Park Road"])
        .includes(['', '', 'Park Road'])
        // .filter(`{{ record.address.startsWith(value[0]) }}`) // 
        .textArea().clearable()
        .placeholder('{{ \'\' + (record.name ? record.name + \'\' : \'\') + \'\'}}')
        .props({
            autoSize: true
        })
        .cell().ellipsis().tooltip().width(150) // width
        .searchable().advancedOnly() // .placeholder("{{ '' + column.title }}")
        .readonly()
        .parse(),
    province: new FormSchema()
        .title('')
        .width(150).center()
        // .column().fixed().right()
        .select({
            province: ['', '', ''],
            // 'province': ['', '', ''],
            '': ['', '', ''],
            '': ['', ''],
            '': ['', '']
        })
        // .props({
        //     allowClear: true
        // })
        .parse(),
    city: new FormSchema()
        .title('')
        .width(150).center()
        // .column().fixed().right()
        .select().keepWatch('province') // 
        .parse()
})

const showItem = argv => {
    const { record } = argv
    // getCurrentInstance()?.appContext.config.globalProperties.$modal.info({ title:'Name', content: record.name })
    Modal.info({
        title: 'Info Title',
        content: JSON.stringify(record, null, 2)
    })
}

watch(() => editable.value, current => {
    // console.log(current, prev)
    // console.log(new CrudOptions(options.value).edit(current).parse())
    options.value = new CrudOptions(options.value).edit(current).parse()
    // console.log("")
    // console.log(JSON.stringify(options.value))
}, {
    deep: true
})
watch(() => searchable.value, current => {
    // console.log(current, prev)
    // console.log(new CrudOptions(options.value).edit(current).parse())
    options.value = new CrudOptions(options.value).search(current).parse()
    // console.log("")
    // console.log(JSON.stringify(options.value))
}, {
    deep: true
})

</script>
